import React, { useState, useEffect } from 'react';
import { useParams, useSearchParams, Link, useNavigate } from 'react-router-dom';
import { ArrowLeft, Calendar, MapPin, Users, CheckCircle, Star, Shield, Loader2, Search, ShoppingCart, X, GraduationCap } from 'lucide-react';
import { motion } from 'framer-motion';
import { Button } from '../components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '../components/ui/card';
import { useCart } from '../context/CartContext';
import apiClient from '../services/api';

// Define TrainingCampSession interface (similar to WorkshopSession)
interface TrainingCampSession {
  id: string;
  unique_identifier: string;
  session_title: string;
  program_type: 'intensive' | 'standard';
  location: string;
  city: string;
  country: string;
  country_code: string;
  full_location: string;
  venue_name?: string;
  organization_logo?: string;
  venue_address?: string;
  location_highlights?: string[];
  date: string;
  start_date: string;
  end_date: string;
  formatted_date: string;
  time: string;
  timezone: string;
  availableSpots: number;
  bookedSpots: number;
  price: number;
  formatted_price: string;
  price_difference?: number;
  is_premium_pricing?: boolean;
  is_discounted_pricing?: boolean;
  is_featured?: boolean;
  is_almost_sold_out?: boolean;
  is_limited_availability?: boolean;
  urgency_level?: 'critical' | 'high' | 'normal';
  capacity_percentage?: number;
  special_notes?: string;
  metadata?: any;
}

// Training camp program data
const premierTrainingCamp = {
  id: 998,
  title: 'Future CEO Training Camp',
  description: 'An intensive leadership and entrepreneurship training program designed to develop the next generation of business leaders',
  price: 799, // Registration fee
  duration: '5 Day Intensive',
  category: 'Leadership Training',
  image: '/images/future-ceo-program.svg',
  features: [
    'Harvard Business School Curriculum',
    'Professional Certificate',
    'Leadership Development',
    'Entrepreneurship Training',
    'Business Case Studies',
    'Networking Opportunities'
  ]
};

// Simple hash function to obscure numeric IDs
const hashId = (id: number): string => {
  const encoded = btoa(`t${id}`);
  return encoded.replace(/[^a-zA-Z0-9]/g, '');
};

// Create session identifier using hashed ID
const createSessionIdentifier = (session: any): string => {
  return hashId(session.id);
};

const FutureCEOProgramBooking: React.FC = () => {
  const { trainingCamp } = useParams<{ trainingCamp: string }>();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const { addToCart, removeFromCart, items } = useCart();
  const [isBooking, setIsBooking] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [trainingCampSessions, setTrainingCampSessions] = useState<TrainingCampSession[]>([]);
  const [trainingCampProgram, setTrainingCampProgram] = useState<any>(null);
  const [selectedProgramType, setSelectedProgramType] = useState<'intensive' | 'standard' | 'all'>('all');
  const [searchQuery, setSearchQuery] = useState('');
  const [refCodeProcessed, setRefCodeProcessed] = useState(false);

  // Handle ref code - separate effect that only runs once
  useEffect(() => {
    const handleRefCode = async () => {
      const refCode = searchParams.get('ref');
      if (refCode && !refCodeProcessed) {
        try {
          await apiClient.storeRefCode(refCode);
          console.log('Ref code stored successfully:', refCode);
          setRefCodeProcessed(true);
        } catch (refError) {
          console.error('Failed to store ref code:', refError);
          // Don't block the main workflow if ref code storage fails
        }
      }
    };

    handleRefCode();
  }, [searchParams, refCodeProcessed]);

  // Load training camp data from API
  useEffect(() => {
    const loadTrainingCampData = async () => {
      try {
        setLoading(true);
        setError(null);

        // Map frontend training camp slugs to backend program slugs
        const slugMapping: { [key: string]: string } = {
          'future-ceo-intensive': 'future-ceo-training-programme',
          'ceo-training-camp': 'future-ceo-training-programme',
          'leadership-camp': 'future-ceo-training-programme',
          'harvard-ceo': 'future-ceo-training-programme'
        };

        const programSlug = trainingCamp ? (slugMapping[trainingCamp] || 'future-ceo-training-programme') : 'future-ceo-training-programme';
        
        // Get schid from URL parameters if present
        const schid = searchParams.get('schid');
        
        const response = await apiClient.getTrainingCampSessions(programSlug, schid || undefined);

        if (response.success) {
          // Transform backend data to frontend format
          const transformedSessions: TrainingCampSession[] = response.data.sessions.map((session: any) => {
            // Calculate capacity percentage if not provided - consistent with detail page
            const availableSpots = session.available_spots || session.remaining_spots || 0;
            const bookedSpots = session.booked_spots || 0;
            const totalSpots = bookedSpots + availableSpots;
            const capacityPercentage = session.capacity_percentage ||
              (totalSpots > 0 ? (bookedSpots / totalSpots) * 100 : 0);

            // Calculate availability flags if not provided from backend
            const isAlmostSoldOut = session.is_almost_sold_out || 
              (capacityPercentage >= 90 && availableSpots > 0 && availableSpots <= 5);
            const isLimitedAvailability = session.is_limited_availability || 
              (capacityPercentage >= 75 && capacityPercentage < 90 && availableSpots > 0);

            return {
              id: session.id.toString(),
              unique_identifier: createSessionIdentifier(session),
              session_title: session.session_title || session.location,
              program_type: session.program_type || 'standard',
              location: session.location,
              city: session.city,
              country: session.country,
              country_code: session.country_code,
              full_location: session.full_location,
              venue_name: session.venue_name,
              organization_logo: session.organization_logo,
              venue_address: session.venue_address,
              location_highlights: session.location_highlights || [],
              date: session.date,
              start_date: session.start_date,
              end_date: session.end_date,
              formatted_date: session.formatted_date,
              time: session.time,
              timezone: session.timezone,
              availableSpots: availableSpots,
              bookedSpots: bookedSpots,
              price: session.effective_price,
              formatted_price: session.formatted_effective_price,
              price_difference: session.price_difference || 0,
              is_premium_pricing: session.is_premium_pricing || false,
              is_discounted_pricing: session.is_discounted_pricing || false,
              is_featured: session.is_featured || false,
              is_almost_sold_out: isAlmostSoldOut,
              is_limited_availability: isLimitedAvailability,
              urgency_level: session.urgency_level || 
                (capacityPercentage >= 90 || availableSpots <= 5 ? 'critical' :
                 capacityPercentage >= 75 || availableSpots <= 15 ? 'high' : 'normal'),
              capacity_percentage: capacityPercentage,
              special_notes: session.special_notes,
              metadata: session.metadata,
              session_type: "training_camp"
            };
          });

          setTrainingCampSessions(transformedSessions);
          setTrainingCampProgram({
            id: response.data.program.id,
            title: response.data.program.title,
            description: response.data.program.description || premierTrainingCamp.description,
            price: parseFloat(response.data.program.price),
            duration: premierTrainingCamp.duration,
            category: premierTrainingCamp.category,
            image: premierTrainingCamp.image,
            features: premierTrainingCamp.features
          });
        } else {
          throw new Error('Failed to load training camp data');
        }
      } catch (err) {
        console.error('Error loading training camp data:', err);
        setError(err instanceof Error ? err.message : 'Failed to load training camp data');
      } finally {
        setLoading(false);
      }
    };

    loadTrainingCampData();
  }, [trainingCamp, searchParams]);

  const handleAddToCart = async (session: TrainingCampSession) => {
    if (!trainingCampProgram) return;

    setIsBooking(true);

    // Add session to cart
    addToCart(trainingCampProgram, 1, session);

    // Brief delay to show loading state
    setTimeout(() => {
      setIsBooking(false);
    }, 500);
  };

  const handleRemoveFromCart = async (session: TrainingCampSession) => {
    if (!trainingCampProgram) return;

    setIsBooking(true);

    // Remove session from cart
    removeFromCart(trainingCampProgram.id, session.id);

    // Brief delay to show loading state
    setTimeout(() => {
      setIsBooking(false);
    }, 500);
  };

  const isSessionInCart = (sessionId: string) => {
    return items.some(item => item.selectedSession?.id === sessionId);
  };

  // Function to extract and format grade information
  const getGradeInfo = (locationHighlights?: string[], metadata?: any) => {
    // First try to get grade from location_highlights
    const gradeHighlight = locationHighlights?.find(highlight =>
      highlight.includes('Grade')
    );

    if (gradeHighlight) {
      return gradeHighlight;
    }

    // Then try to get from metadata.grade_levels
    if (metadata?.grade_levels && Array.isArray(metadata.grade_levels)) {
      const grades = metadata.grade_levels;
      if (grades.length === 1) {
        return `Grade ${grades[0]}`;
      } else if (grades.length > 1) {
        return `Grades ${grades.join(', ')}`;
      }
    }

    return null;
  };

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('en-US', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  const formatDateRange = (startDate: string, endDate: string) => {
    const start = new Date(startDate);
    const end = new Date(endDate);
    
    const startFormatted = start.toLocaleDateString('en-US', {
      month: 'short',
      day: 'numeric'
    });
    
    const endFormatted = end.toLocaleDateString('en-US', {
      month: 'short',
      day: 'numeric',
      year: 'numeric'
    });
    
    return `${startFormatted} - ${endFormatted}`;
  };

  const styleGenderText = (text: string) => {
    const parts = text.split(/\b(girls?|boys?)\b/gi);
    
    return parts.map((part, index) => {
      const lowerPart = part.toLowerCase();
      if (lowerPart === 'girl' || lowerPart === 'girls') {
        return <span key={index} className="text-pink-600 font-bold">{part}</span>;
      } else if (lowerPart === 'boy' || lowerPart === 'boys') {
        return <span key={index} className="text-blue-600 font-bold">{part}</span>;
      }
      return part;
    });
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-blue-50/30 to-indigo-50/50">
      {/* Header */}
      <section className=" relative overflow-hidden bg-gradient-to-br from-[#1f2444] via-[#2d3d66] to-[#9f162e] text-white">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12 md:py-16">
          <Button variant="outline" asChild className="mb-6 border-gray-300 bg-white text-gray-700 hover:bg-gray-50 hover:text-gray-900">
            <Link to="/harvard-future-ceo">
              <ArrowLeft className="h-4 w-4 mr-2" /> Back to Program Details
            </Link>
          </Button>

          <motion.div
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8 }}
          >
            <div className="flex items-center mb-4">
              <Star className="h-6 w-6 text-yellow-400 mr-2" />
              <span className="text-yellow-400 font-semibold">Premier Training Camp</span>
            </div>
            <h1 className="text-3xl md:text-4xl lg:text-5xl font-bold mb-4 leading-tight">
              {trainingCampProgram?.title || premierTrainingCamp.title}
            </h1>
            <p className="text-lg md:text-xl text-gray-200 max-w-3xl leading-relaxed">
              {trainingCampProgram?.description || premierTrainingCamp.description}
            </p>
          </motion.div>
        </div>
      </section>

      {/* Main Content */}
      <section className="py-12 md:py-16">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          {loading ? (
            <div className="flex justify-center items-center py-16">
              <div className="flex items-center space-x-4">
                <Loader2 className="h-8 w-8 animate-spin text-blue-600" />
                <span className="text-lg text-gray-600">Loading training camp sessions...</span>
              </div>
            </div>
          ) : error ? (
            <div className="text-center py-16">
              <div className="bg-red-50 border border-red-200 rounded-lg p-6 max-w-md mx-auto">
                <h3 className="text-lg font-semibold text-red-800 mb-2">Error Loading Training Camp</h3>
                <p className="text-red-600 mb-4">{error}</p>
                <Button
                  onClick={() => window.location.reload()}
                  variant="outline"
                  className="border-red-300 text-red-700 hover:bg-red-50"
                >
                  Try Again
                </Button>
              </div>
            </div>
          ) : (
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 lg:gap-8">

              {/* Session Selection */}
              <div className="lg:col-span-2">
                <motion.div
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.6 }}
                >
                  <h2 className="text-xl sm:text-2xl md:text-3xl font-bold text-gray-900 mb-4 sm:mb-6">
                    Select Your Session
                  </h2>
                  <p className="text-sm sm:text-base text-gray-600 mb-4 sm:mb-6">
                    Choose from our upcoming training camp sessions in premium locations worldwide.
                  </p>

                  {/* Search Bar */}
                  <div className="mb-6">
                    <div className="relative">
                      <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                      <input
                        type="text"
                        placeholder="Search by location, venue, or country..."
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                        className="w-full pl-10 pr-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 text-sm"
                      />
                    </div>
                  </div>

                  {/* Program Type Filter - Improved Mobile */}
                  <div className="mb-8">
                    <div className="flex flex-col sm:flex-row gap-2 sm:gap-3">
                      <button
                        onClick={() => setSelectedProgramType('all')}
                        className={`px-3 sm:px-4 py-2 rounded-lg font-medium transition-all text-sm sm:text-base ${selectedProgramType === 'all'
                          ? 'bg-[#1f2444] text-white shadow-lg'
                          : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                          }`}
                      >
                        All Sessions ({trainingCampSessions.length})
                      </button>
                      <button
                        onClick={() => setSelectedProgramType('standard')}
                        className={`px-3 sm:px-4 py-2 rounded-lg font-medium transition-all flex items-center justify-center text-sm sm:text-base ${selectedProgramType === 'standard'
                          ? 'bg-blue-600 text-white shadow-lg'
                          : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                          }`}
                      >
                        <span className="sm:hidden"> Standard ({trainingCampSessions.filter(s => s.program_type === 'standard').length})</span>
                        <span className="hidden sm:inline"> Standard ({trainingCampSessions.filter(s => s.program_type === 'standard').length})</span>
                      </button>
                      <button
                        onClick={() => setSelectedProgramType('intensive')}
                        className={`px-3 sm:px-4 py-2 rounded-lg font-medium transition-all flex items-center justify-center text-sm sm:text-base ${selectedProgramType === 'intensive'
                          ? 'bg-gradient-to-r from-amber-500 to-orange-600 text-white shadow-lg'
                          : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                          }`}
                      >
                        <span className="sm:hidden"> Intensive ({trainingCampSessions.filter(s => s.program_type === 'intensive').length})</span>
                        <span className="hidden sm:inline"> Intensive ({trainingCampSessions.filter(s => s.program_type === 'intensive').length})</span>
                      </button>
                    </div>
                  </div>

                  <div className="space-y-4">
                    {trainingCampSessions
                      .filter(session => {
                        // Program type filter
                        const programTypeMatch = selectedProgramType === 'all' ||
                          session.program_type === selectedProgramType;

                        // Search filter
                        const searchMatch = searchQuery === '' ||
                          session.location.toLowerCase().includes(searchQuery.toLowerCase()) ||
                          session.venue_name?.toLowerCase().includes(searchQuery.toLowerCase()) ||
                          session.country?.toLowerCase().includes(searchQuery.toLowerCase()) ||
                          session.city?.toLowerCase().includes(searchQuery.toLowerCase());

                        return programTypeMatch && searchMatch;
                      })
                      .map((session, index) => (
                        <motion.div
                          key={session.id}
                          initial={{ opacity: 0, y: 20 }}
                          animate={{ opacity: 1, y: 0 }}
                          transition={{ duration: 0.4, delay: index * 0.1 }}
                        >
                          <Card
                            className={`transition-all duration-300 hover:shadow-xl border-2 relative overflow-hidden ${session.availableSpots <= 0 ? 'cursor-not-allowed' : 'cursor-pointer'} ${isSessionInCart(session.id)
                              ? 'border-green-400 bg-green-50/50 shadow-md'
                              : session.program_type === 'intensive'
                                ? 'border-amber-200 hover:border-amber-300 bg-gradient-to-br from-amber-50/50 to-orange-50/50'
                                : 'border-gray-200 hover:border-gray-300'
                              }`}
                            onClick={() => {
                              if (session.availableSpots > 0) {
                                navigate(`/harvard-future-ceo-booking/${trainingCamp}/session/${session.unique_identifier || session.id}`);
                              }
                            }}
                          >
                            <CardContent className="p-4 sm:p-6 relative">

                              <div className="space-y-6">
                                {/* Top Section: Title + Badges + Price + Logo */}
                                <div className="flex items-start justify-between gap-4">
                                  <div className="flex-1 min-w-0">
                                    {/* Session Title */}
                                    <div className="mb-3">
                                      <h3 className="text-lg sm:text-xl font-bold text-gray-900 line-clamp-2 leading-tight">
                                        {session.session_title || session.location}
                                      </h3>
                                    </div>

                                    {/* Badges Row */}
                                    <div className="flex items-center flex-wrap gap-2">
                                      {/* Program Type Badge */}
                                      <div className={`text-xs px-2 py-1 rounded-full font-bold ${
                                        session.program_type === 'intensive'
                                          ? 'bg-gradient-to-r from-amber-400 to-orange-500 text-white'
                                          : 'bg-gradient-to-r from-blue-400 to-blue-600 text-white'
                                      }`}>
                                        {session.program_type === 'intensive' ? ' INTENSIVE' : ' STANDARD'}
                                      </div>

                                      {/* Featured Badge */}
                                      {session.is_featured && (
                                        <div className="bg-gradient-to-r from-purple-500 to-pink-500 text-white text-xs px-2 py-1 rounded-full font-bold">
                                           FEATURED
                                        </div>
                                      )}

                                      {/* Premium/Discounted Badge */}
                                      {session.is_premium_pricing && (
                                        <div className="bg-gradient-to-r from-gold-400 to-gold-600 text-white text-xs px-2 py-1 rounded-full font-bold">
                                           PREMIUM
                                        </div>
                                      )}
                                      {session.is_discounted_pricing && (
                                        <div className="bg-gradient-to-r from-green-500 to-green-600 text-white text-xs px-2 py-1 rounded-full font-bold">
                                           DISCOUNTED
                                        </div>
                                      )}
                                    </div>
                                  </div>

                                  {/* Price + Logo - Top Right */}
                                  <div className="flex flex-col items-center gap-3">
                                    <div className="text-2xl lg:text-3xl font-bold text-blue-600 text-center">
                                      {session.formatted_price}
                                    </div>

                                    {/* Organization Logo */}
                                    {session.organization_logo && (
                                      <div className="relative">
                                        <img
                                          src={session.organization_logo}
                                          alt="Organization"
                                          className="w-16 h-16 sm:w-20 sm:h-20 object-contain rounded-lg shadow-sm bg-white p-2 border border-gray-100"
                                          onError={(e) => {
                                            const target = e.target as HTMLImageElement;
                                            target.style.display = 'none';
                                          }}
                                        />
                                        {/* Verification Badge */}
                                        <div className="absolute -top-1 -right-1 w-6 h-6 bg-blue-600 rounded-full flex items-center justify-center">
                                          <CheckCircle className="w-4 h-4 text-white" />
                                        </div>
                                      </div>
                                    )}
                                  </div>
                                </div>

                                {/* Middle Section: Key Details (Improved Hierarchy) */}
                                <div className="space-y-5">
                                  {/* Location Block */}
                                  <div className="flex items-center gap-3">
                                    <MapPin className="h-5 w-5 text-blue-600 flex-shrink-0" />
                                    <div className="flex-1 items-center">
                                      <div className="font-semibold text-gray-900 text-sm sm:text-base">
                                        {session.full_location}
                                      </div>
                                      {session.venue_name && (
                                        <div className="text-xs sm:text-sm text-gray-600 mt-1">{session.venue_name}</div>
                                      )}
                                    </div>
                                  </div>

                                  {/* Date & Time Block */}
                                  <div className="flex items-center gap-3">
                                    <Calendar className="h-5 w-5 text-blue-600 flex-shrink-0" />
                                    <div className="flex-1">
                                      <div className="font-semibold text-gray-900 text-sm sm:text-base">
                                        {session.start_date && session.end_date 
                                          ? formatDateRange(session.start_date, session.end_date)
                                          : formatDate(session.date)
                                        }
                                      </div>
                                      <div className="text-xs sm:text-sm text-gray-600 mt-1">
                                        {session.time} {session.timezone && `(${session.timezone})`}
                                      </div>
                                    </div>
                                  </div>

                                  {/* Grade Information Block */}
                                  {getGradeInfo(session.location_highlights, session.metadata) && (
                                    <div className="flex items-center gap-3">
                                      <GraduationCap className="h-5 w-5 text-blue-600 flex-shrink-0" />
                                      <div className="flex-1">
                                        <div className="font-semibold text-gray-900 text-sm sm:text-base">
                                          {styleGenderText(getGradeInfo(session.location_highlights, session.metadata) || '')}
                                        </div>
                                      </div>
                                    </div>
                                  )}

                                  {/* Availability Block */}
                                  <div className="flex items-start gap-3">
                                    <Users className="h-5 w-5 text-blue-600 flex-shrink-0 mt-0.5" />
                                    <div className="flex-1">
                                      <div className="flex items-center justify-between">
                                        <span className="font-semibold text-gray-900 text-sm sm:text-base">
                                          {session.availableSpots > 0 ? (
                                            <>
                                              {session.availableSpots} spot{session.availableSpots !== 1 ? 's' : ''} available
                                            </>
                                          ) : (
                                            <span className="text-red-600 font-bold">Fully Booked</span>
                                          )}
                                        </span>
                                        {session.capacity_percentage !== undefined && session.capacity_percentage > 0 && (
                                          <span className={`text-xs px-2 py-1 rounded-full font-semibold ${
                                            session.capacity_percentage >= 90 
                                              ? 'bg-red-100 text-red-800' 
                                              : session.capacity_percentage >= 75 
                                                ? 'bg-yellow-100 text-yellow-800' 
                                                : 'bg-green-100 text-green-800'
                                          }`}>
                                            {Math.round(session.capacity_percentage)}% full
                                          </span>
                                        )}
                                      </div>
                                      
                                      {/* Capacity Bar */}
                                      {session.capacity_percentage !== undefined && session.capacity_percentage > 0 && (
                                        <div className="mt-2">
                                          <div className="w-full bg-gray-200 rounded-full h-2">
                                            <div 
                                              className={`h-2 rounded-full transition-all duration-300 ${
                                                session.capacity_percentage >= 90 
                                                  ? 'bg-gradient-to-r from-red-500 to-red-600' 
                                                  : session.capacity_percentage >= 75 
                                                    ? 'bg-gradient-to-r from-yellow-500 to-orange-500' 
                                                    : 'bg-gradient-to-r from-green-500 to-green-600'
                                              }`}
                                              style={{ width: `${Math.min(session.capacity_percentage, 100)}%` }}
                                            />
                                          </div>
                                        </div>
                                      )}
                                    </div>
                                  </div>
                                </div>

                                {/* Enhanced Scarcity Indicators */}
                                {session.availableSpots > 0 && (session.is_almost_sold_out || session.is_limited_availability) && (
                                  <div className="pt-2">
                                    {session.is_almost_sold_out && (
                                      <div className="bg-red-50 border border-red-200 rounded-lg p-3">
                                        <div className="flex items-center">
                                          <div className="text-red-600 font-bold text-sm"> Almost Sold Out - Only {session.availableSpots} spots left!</div>
                                        </div>
                                      </div>
                                    )}
                                    {!session.is_almost_sold_out && session.is_limited_availability && (
                                      <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-3">
                                        <div className="flex items-center">
                                          <div className="text-yellow-700 font-bold text-sm"> Limited Availability - {session.availableSpots} spots remaining</div>
                                        </div>
                                      </div>
                                    )}
                                  </div>
                                )}

                                {session.availableSpots > 0 && (
                                  <>
                                    {/* Action Buttons Row */}
                                    <div className="flex items-center justify-between pt-4 border-t border-gray-100">
                                      <Link
                                        to={`/harvard-future-ceo-booking/${trainingCamp}/session/${session.unique_identifier || session.id}`}
                                        className="text-blue-600 hover:text-blue-800 font-semibold text-sm transition-colors"
                                        onClick={(e) => e.stopPropagation()}
                                      >
                                        View Details 
                                      </Link>
                                      
                                      <div className="flex items-center gap-3">
                                        {isSessionInCart(session.id) ? (
                                          <Button
                                            onClick={(e) => {
                                              e.stopPropagation();
                                              handleRemoveFromCart(session);
                                            }}
                                            disabled={isBooking}
                                            variant="outline"
                                            size="sm"
                                            className="border-red-300 text-red-700 hover:bg-red-50 font-semibold"
                                          >
                                            <X className="h-4 w-4 mr-1" />
                                            Remove
                                          </Button>
                                        ) : (
                                          <Button
                                            onClick={(e) => {
                                              e.stopPropagation();
                                              handleAddToCart(session);
                                            }}
                                            disabled={isBooking}
                                            size="sm"
                                            className="bg-gradient-to-r from-[#1f2444] to-[#9f162e] hover:from-[#9f162e] hover:to-[#1f2444] text-white font-semibold shadow-lg"
                                          >
                                            <ShoppingCart className="h-4 w-4 mr-1" />
                                            Add to Cart
                                          </Button>
                                        )}
                                      </div>
                                    </div>
                                  </>
                                 )}
                              </div>
                            </CardContent>
                            
                            {/* Sold Out Overlay - On Top */}
                            {session.availableSpots <= 0 && (
                              <div className="absolute inset-0 z-50 flex items-center justify-center pointer-events-none bg-gray-900/50 backdrop-blur-sm">
                                <div className="bg-white rounded-lg p-6 shadow-xl border-2 border-red-500 text-center">
                                  <div className="text-red-600 font-bold text-xl mb-2"> FULLY BOOKED</div>
                                  <div className="text-gray-700 font-medium">This training camp session is full</div>
                                  <div className="text-sm text-gray-500 mt-1">Check other available sessions</div>
                                </div>
                              </div>
                            )}
                          </Card>
                        </motion.div>
                      ))}
                  </div>
                </motion.div>
              </div>

              {/* Training Camp Info Sidebar */}
              <div className="lg:col-span-1">
                <motion.div
                  initial={{ opacity: 0, x: 20 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ duration: 0.6, delay: 0.2 }}
                  className="lg:sticky lg:top-6"
                >
                  <Card className="shadow-lg lg:shadow-xl border-0 bg-white">
                    <CardHeader className="pb-4">
                      <CardTitle className="flex items-center justify-between text-lg">
                        <div className="flex items-center">
                          <Shield className="h-5 w-5 mr-2 text-blue-600" />
                          Training Camp Details
                        </div>
                        {items.length > 0 && (
                          <Link
                            to="/cart"
                            className="bg-blue-100 text-blue-800 text-xs px-3 py-1.5 rounded-full font-semibold hover:bg-blue-200 transition-colors"
                          >
                            {items.length} in cart
                          </Link>
                        )}
                      </CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-6">
                      {/* Training Camp Details */}
                      <div>
                        <img
                          src={trainingCampProgram?.image || premierTrainingCamp.image}
                          alt={trainingCampProgram?.title || premierTrainingCamp.title}
                          className="w-full h-32 object-cover rounded-lg mb-4"
                          style={{ aspectRatio: '4/3' }}
                        />
                        <h3 className="font-bold text-gray-900 mb-2">{trainingCampProgram?.title || premierTrainingCamp.title}</h3>
                        <p className="text-sm text-gray-600 mb-4">{trainingCampProgram?.description || premierTrainingCamp.description}</p>

                        {/* Features */}
                        <div className="space-y-2">
                          {(trainingCampProgram?.features || premierTrainingCamp.features).slice(0, 4).map((feature: string, index: number) => (
                            <div key={index} className="flex items-center text-sm text-gray-700">
                              <CheckCircle className="h-4 w-4 text-green-600 mr-2 flex-shrink-0" />
                              {feature}
                            </div>
                          ))}
                        </div>
                      </div>

                      {/* Cart CTA */}
                      {items.length > 0 && (
                        <div className="border-t pt-6">
                          <Link to="/cart">
                            <Button className="w-full bg-gradient-to-r from-[#1f2444] to-[#9f162e] hover:from-[#9f162e] hover:to-[#1f2444] text-white font-bold py-3 shadow-lg">
                              <ShoppingCart className="h-4 w-4 mr-2" />
                              View Cart ({items.length})
                            </Button>
                          </Link>
                        </div>
                      )}

                      {/* Trust Indicators */}
                      <div className="border-t pt-6 space-y-3">
                        <div className="flex items-center text-sm text-gray-600">
                          <Shield className="h-4 w-4 mr-2 text-green-600" />
                          Professional Certificate
                        </div>
                        <div className="flex items-center text-sm text-gray-600">
                          <Star className="h-4 w-4 mr-2 text-yellow-500" />
                          Harvard Business School Curriculum
                        </div>
                        <div className="flex items-center text-sm text-gray-600">
                          <CheckCircle className="h-4 w-4 mr-2 text-blue-600" />
                          Certificate Included
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                </motion.div>
              </div>
            </div>
          )}
        </div>
      </section>

      {/* Program Types Explanation */}
      <section className="py-12 bg-gray-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <motion.div
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
            className="text-center mb-8"
          >
            <h2 className="text-2xl md:text-3xl font-bold text-gray-900 mb-4">
              Information about the Program Format
            </h2>
            <p className="text-gray-600 max-w-2xl mx-auto">
              We offer two program formats to suit different learning preferences and schedules
            </p>
          </motion.div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-6 max-w-4xl mx-auto">
            {/* Standard Program */}
            <motion.div
              initial={{ opacity: 0, x: -30 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.6, delay: 0.2 }}
              className="bg-white rounded-xl p-6 border border-gray-200 shadow-sm"
            >
              <div className="flex items-center mb-4">
                <div className="bg-gradient-to-r from-blue-400 to-blue-600 text-white text-xs px-3 py-1 rounded-full font-bold">
                   STANDARD
                </div>
              </div>
              <h3 className="text-lg font-bold text-gray-900 mb-2">5-Day Intensive Program</h3>
              <p className="text-gray-600 text-sm mb-4">
                Focused on core leadership and entrepreneurship skills in an intensive format. Perfect for aspiring business leaders.
              </p>
              <ul className="space-y-2 text-sm text-gray-600">
                <li className="flex items-center">
                  <CheckCircle className="h-4 w-4 text-green-600 mr-2" />
                  5 days of intensive training
                </li>
                <li className="flex items-center">
                  <CheckCircle className="h-4 w-4 text-green-600 mr-2" />
                  Core methodologies and techniques
                </li>
                <li className="flex items-center">
                  <CheckCircle className="h-4 w-4 text-green-600 mr-2" />
                  Certificate of completion
                </li>
                <li className="flex items-center">
                  <CheckCircle className="h-4 w-4 text-green-600 mr-2" />
                  Networking opportunities
                </li>
              </ul>
            </motion.div>

            {/* Intensive Program */}
            <motion.div
              initial={{ opacity: 0, x: 30 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.6, delay: 0.4 }}
              className="bg-gradient-to-br from-amber-50 to-orange-50 rounded-xl p-6 border border-amber-200 shadow-sm relative overflow-hidden"
            >
              <div className="absolute top-0 right-0 w-20 h-20 bg-gradient-to-br from-amber-400/10 to-orange-400/10 rounded-full -mr-10 -mt-10"></div>
              <div className="flex items-center mb-4">
                <div className="bg-gradient-to-r from-amber-400 to-orange-500 text-white text-xs px-3 py-1 rounded-full font-bold">
                   INTENSIVE
                </div>
                <span className="ml-2 text-xs text-amber-700 font-semibold">PREMIUM</span>
              </div>
              <h3 className="text-lg font-bold text-gray-900 mb-2">7-Day Extended Experience</h3>
              <p className="text-gray-600 text-sm mb-4">
                Comprehensive program with personalized mentoring, exclusive networking, and advanced methodologies.
              </p>
              <ul className="space-y-2 text-sm text-gray-600">
                <li className="flex items-center">
                  <CheckCircle className="h-4 w-4 text-green-600 mr-2" />
                  Up to 7 days of comprehensive training
                </li>
                <li className="flex items-center">
                  <CheckCircle className="h-4 w-4 text-green-600 mr-2" />
                  Personal coaching sessions
                </li>
                <li className="flex items-center">
                  <CheckCircle className="h-4 w-4 text-green-600 mr-2" />
                  Exclusive networking dinner with instructors
                </li>
                <li className="flex items-center">
                  <CheckCircle className="h-4 w-4 text-green-600 mr-2" />
                  Reference letter opportunity
                </li>
                <li className="flex items-center">
                  <CheckCircle className="h-4 w-4 text-green-600 mr-2" />
                  Premium access
                </li>
              </ul>
            </motion.div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default FutureCEOProgramBooking;